import React from 'react';
import Barcode from 'react-barcode';
import { ParcelCharge } from "./../../../../../utils/constants";
import "./PrintDesign.scss";

const PrintDesign = ({ senderDetails, receiverDetails, packageList, documentList, isDocument, selectedCurrency }) => {

    const getDocumentWeightRate = (item) => {
        let rate = ParcelCharge.documentRate;

        if (item.weight >= 2) {
            rate = ParcelCharge.packageRate;
        }

        return item.weight * item.noOfPiece * rate;
    }

    const getPackageVolumeRate = (item) => {
        let rate = ParcelCharge.packageFixedRate;
        let volume = ((item.length * item.width * item.height)/ParcelCharge.dimensionDivider);
        if (volume >= 2) {
            rate = item.volume * item.itemsCount * ParcelCharge.packageRate;
        }

        return rate;
    }

    const getPackageWeightRate = (item) => {
        return item.weight * item.itemsCount * ParcelCharge.packageRate;
    }   

    return (
        // TODO: Need to make table as per Screenshot
        <div className="mtrx-print-details">
            {/* <Barcode value="barcode-example" /> */}
            {/* 1. Shipper Address */}
            <div style={{ borderTop: '2px solid #000000', display: 'flex', flexDirection: 'row', 
                    paddingRight: 50, paddingLeft: 50, paddingTop: 10, paddingBottom: 10, fontSize: 12, color:'#454343' }}>
                <div style={{ flex: 1}}>
                    <div style={{ fontSize: 14, fontWeight: '600', color: '#000000' }} >Shipper : </div>
                    <div>{senderDetails.company}</div>
                    <div>{senderDetails.name}</div>
                    <div>{senderDetails.addressLine1 + "," + senderDetails.addressLine2}</div>
                    <div>{senderDetails.city + " - " + senderDetails.postalCode }</div>
                    <div>{senderDetails.state + "," + senderDetails.country}</div>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ fontSize: 14, fontWeight: '600', color: '#000000' }} >Contact : </div>
                    <div>{senderDetails.phone}</div>
                    <div>{senderDetails.email}</div>
                </div>
            </div>

            {/* 1. Consignee Address */}
            <div style={{ borderTop: '2px solid #000000', display: 'flex', flexDirection: 'row', 
                    paddingRight: 50, paddingLeft: 50, paddingTop: 10, paddingBottom: 10, fontSize: 12, color:'#454343' }}>
                <div style={{ flex: 1}}>
                    <div style={{ fontSize: 14, fontWeight: '600', color: '#000000' }} >Receiver : </div>
                    <div>{receiverDetails.company}</div>
                    <div>{receiverDetails.name}</div>
                    <div>{receiverDetails.addressLine1 + "," + receiverDetails.addressLine2}</div>
                    <div>{receiverDetails.city + " - " + receiverDetails.postalCode }</div>
                    <div>{receiverDetails.state + "," + receiverDetails.country}</div>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ fontSize: 14, fontWeight: '600', color: '#000000' }} >Contact : </div>
                    <div>{receiverDetails.phone}</div>
                    <div>{receiverDetails.email}</div>
                </div>
            </div>
            
            {/* 3. Package Item Details */}
            <div>
                { isDocument ?
                        (documentList.length > 0 &&
                            <table className="item-list-table" style={{  border: '1px solid #9d9d9d', borderCollapse: 'collapse'}}>
                                <thead className="table-header" style={{ border: '1px solid #9d9d9d', backgroundColor: '#E4E4E4' }}>
                                    <tr style={{ border: '1px solid #9d9d9d', color: '#000000' }}>
                                        <th>S.NO</th>
                                        <th>WEIGHT (kg)</th>
                                        <th>NO.OF PIECES</th>
                                        <th>WEIGHT RATE</th>
                                        <th>NOTES</th>
                                    </tr>
                                </thead>
                                <tbody className="table-body" style={{ border: '1px solid #9d9d9d' }}>
                                    {
                                        documentList.map((item, index)=> {
                                            return <tr key={index+1}>
                                                <td>{index+1}</td>
                                                <td>{item.weight}</td>
                                                <td>{item.noOfPiece}</td>
                                                <td>{getDocumentWeightRate(item)}</td>
                                                <td>{item.notes}</td>
                                            </tr> 
                                        })
                                    }
                                </tbody>
                            </table>)
                        :
                        (packageList?.length > 0 &&
                        <table className="item-list-table" style={{  border: '1px solid #9d9d9d', borderCollapse: 'collapse'}}>
                            <thead className="table-header" style={{ border: '1px solid #9d9d9d', backgroundColor: '#E4E4E4' }}>
                                <tr style={{ border: '1px solid #9d9d9d', color: '#000000' }}>
                                    <th>S.NO</th>
                                    <th>WEIGHT</th>
                                    <th>DIM in cm (LxWxH)</th>
                                    <th>NO.OF ITEMS</th>
                                    <th>VOLUME RATE</th>
                                    <th>WEIGHT RATE</th>
                                    <th>NOTES</th>
                                </tr>
                            </thead>
                            <tbody className="table-body">
                                {
                                    packageList.map((item, index)=> {
                                        return <tr key={index+1}>
                                            <td>{index+1}</td>
                                            <td>{item.weight}</td>
                                            <td>{item.length}x{item.width}x{item.height}</td>
                                            <td>{item.itemsCount}</td>
                                            <td>{getPackageVolumeRate(item)}</td>
                                            <td>{getPackageWeightRate(item)}</td>
                                            <td>{item.notes}</td>
                                        </tr> 
                                    })
                                }
                            </tbody>
                        </table>)
                    }
            </div>

            <div style={{ borderBottom: '2px solid #000000', display: 'flex', justifyContent: 'end', 
                paddingTop: 30, paddingBottom: 10, paddingRight: 50  }}>
               <div style={{ textAlign: 'right' }}>
                   Total = 20 {selectedCurrency}
                </div>
            </div>
            <div style={{ borderBottom: '2px solid #000000', paddingTop: 30, paddingBottom: 10, paddingRight: 50,
                      display: 'flex', flexDirection: 'row',justifyContent: 'space-between' }}>
                <Barcode value="MAT-12345678" height="60" />
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'end', fontSize: 14, fontStyle: 'italic' }}>
                    Signature
                </div>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row',justifyContent: 'space-between' }}>
                <div style={{ fontSize: 8, fontStyle: 'italic' }}>
                    Reg Office: Matrix Freight Forwarders Pte Ltd <br />
                    95 Airport Cargo Road #02-224 Singapore 819461.
                </div>
                <div style={{ fontSize: 8, fontStyle: 'italic' }}>
                    Contact: +91 9xxxxx88xxx<br />
                    Email: zzzz@yyy.com
                </div>
            </div>
            

        </div>
    )
};

export { PrintDesign };