import React from 'react';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import { PrintDesign } from "./print-design/PrintDesign";
import { ReactComponent as CompanyLogo } from './../../../../assets/images/matrix_logo.svg';
import "./OrderPrint.scss";
import { getDDMMMYYYYDate } from '../../../../utils/date.util';

const OrderPrint = ({selectedOrder, onClose}) => {

    const onClosePopup = () => {
        onClose();
    }

    return (
        <React.Fragment>
            { /*  Order Preview */}
            <Dialog open={true} onClose={() => { onClosePopup() }} className="preview-window">
                <DialogTitle style={{ display: 'flex', flexDirection: 'row',justifyContent: 'space-between', 
                        paddingLeft: 50, paddingRight: 50, paddingTop: 30 }}>
                <div>
                    <div style={{ fontSize: 18, fontWeight: '700'}}>*WAYBILL DOC*</div> 
                    <div style={{ fontSize: 10 }}>*To be attached to the package</div> 
                    <div style={{ fontSize: 12 }}> On {getDDMMMYYYYDate(new Date()) } </div> 
                </div>
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                    <CompanyLogo />
                </div>
                </DialogTitle>
                <DialogContent style={{ display: 'flex', justifyContent: 'center' }}>
                    <DialogContentText>
                        <PrintDesign 
                            senderDetails={selectedOrder?.sender} 
                            receiverDetails={selectedOrder?.receiver} 
                            packageList={selectedOrder?.packs} 
                            documentList={selectedOrder?.docs} 
                            isDocument={(selectedOrder?.docs && selectedOrder?.docs.lenght > 0) ? true : false} 
                            selectedCurrency={selectedOrder?.currency} />
                    </DialogContentText>
                </DialogContent>
                <DialogActions style={{ padding: '30px' }}>
                    {/* <Button variant="contained" onClick={goToOrderList}>Go To Order List</Button>
                    <Button variant="contained" onClick={createNewOrder} autoFocus>Create New Order</Button> */}
                </DialogActions>
            </Dialog>
        </React.Fragment>
    );

}

export { OrderPrint };