const MonthType = {
    1: 'Jan',
    2: 'Feb',
    3: 'Mar',
    4: 'Apr',
    5: 'May',
    6: 'Jun',
    7: 'Jul',
    8: 'Aug',
    9: 'Sep',
    10: 'Oct',
    11: 'Nov',
    12: 'Dec',
  }

  export const getDDMMMYYYYDate = (date) => {
    const day = date.getDate();
    const dayString = day < 10 ? `0${day}` : day;
    const month = date.getMonth() + 1;
    const monthString = MonthType[month];
    return `${dayString} ${monthString} ${date.getFullYear()}`;
  };