import { STATUSCODE } from "../../../utils/constants";

const getOrderResponseTransformer = (response) => {
    // TODO: Response should have the info about whether its document or package.
    const data = JSON.parse(response.data);
    const resultCode = data.result_code;
    const responseResult = data.results;

    if (resultCode !== STATUSCODE.SUCCESS) {
        return data;
    }

    // Transform this only if it success.
    let result = {sender: null, receiver: null, packs: [], docs: [], isDocument: false, currency: "" }
    result.sender = {
        name: responseResult.send_name,
        company: responseResult.send_cmpny_name,
        proofId: responseResult.send_proof_id,
        proofDocId: responseResult.send_proof_doc,
        addressLine1: responseResult.send_address.split(",")[0],
        addressLine2: responseResult.send_address.split(",")[1],
        city: responseResult.send_city,
        state: responseResult.send_state,
        country: responseResult.send_country,
        postalCode: responseResult.send_pincode,
        phone: responseResult.send_phone,
        email: responseResult.send_email,
    };
    result.receiver = {
        name: responseResult.rece_name,
        // company: responseResult.send_cmpny_name,
        proofId: responseResult.rece_proof_id,
        proofDocId: responseResult.rece_proof_doc,
        addressLine1: responseResult.rece_address.split(",")[0],
        addressLine2: responseResult.rece_address.split(",")[1],
        city: responseResult.rece_city,
        state: responseResult.rece_state,
        country: responseResult.rece_country,
        postalCode: responseResult.rece_pincode,
        phone: responseResult.rece_phone,
        email: responseResult.rece_email,
    };
    result.packs = [...responseResult.packs]
    result.docs = [...responseResult.docs];
    result.currency = responseResult.ord_currency;
    result.isDocument = (responseResult.docs.lenght > 0) ? true : false;

    return {
        result_code: resultCode,
        results: {...result}
    };
}

const addOrderRequestTransformer = (loginID, token, roleNum, orderDetails) => {
    // console.log('orderDetails:', orderDetails);
    const uploadedDocuments = [];
    const {senderDetails, receiverDetails, itemList, currency, isDocument, status } = orderDetails;
    const result = {
        token: token,
        login_id: loginID,

        send_cmpny_name: senderDetails.company,
        send_name: senderDetails.name,
        send_phone: senderDetails.phone,
        send_email: senderDetails.email,
        send_address: senderDetails.addressLine1 + "," + senderDetails.addressLine2,
        send_pincode: senderDetails.postalCode,
        send_city: senderDetails.city,
        send_state: senderDetails.state,
        send_country: senderDetails.country,
        send_proof_id: senderDetails.proofId,
        send_proof_doc: senderDetails.proofDocId,

        // MISSING: rece_name: receiverDetails.company,
        rece_name: receiverDetails.name,
        rece_phone: receiverDetails.phone,
        rece_email: receiverDetails.email,
        rece_address:  receiverDetails.addressLine1 + "," + receiverDetails.addressLine2,
        rece_pincode: receiverDetails.postalCode,
        rece_city: receiverDetails.city,
        rece_state: receiverDetails.state,
        rece_country: receiverDetails.country,
        rece_proof_id: receiverDetails.proofId,
        rece_proof_doc: receiverDetails.proofDocId,

        trans_mode: "1", // Courier/Air 
        // REQUIRED?
        declared_val: 555,
        ord_currency: currency,
        // MISSING:  Status SAVE/SUBMIT is missing...

        packs: [],
        docs: [],
    };

    if (isDocument) {
        itemList.forEach((item, index) => {
            result.docs.push({
                weight: item.weight,
                pack_desc: item.notes || "",
                no_of_items: item.noOfPiece
            });
        });

    } else {
        itemList.forEach(item => {
            result.packs.push({
                weight: item.weight,
                length: item.length,
                width: item.width,
                height: item.height,
                no_of_items: item.itemsCount,
                pack_desc: item.notes || "",
            });
        });
    }

    console.log('Formatted Request:', result);
    return result;
}


export { getOrderResponseTransformer, addOrderRequestTransformer };