import { AxiosInstance } from "./axios-instance";
import { URL } from './url';

const getAllOrder = (loginID, token, roleNum) => {
    const requestBody = { login_id: loginID, token, number: roleNum };
    return AxiosInstance.post(URL.GET_ALL_ORDER, requestBody);
};

const  getOrder = (loginID, token, invoiceId) => {
    const requestBody = { login_id: loginID, token, invoice_value: invoiceId };
    return AxiosInstance.post(URL.GET_ORDER, requestBody);
};

const addOrder = (requestBody) => {
    // console.log('requestBody in service:', requestBody);
    return AxiosInstance.post(URL.NEW_ORDER, requestBody);
};

const updateOrder = (loginID, token, roleNum, orderDetails) => {
    const requestBody = { ...orderDetails };
    return AxiosInstance.post(URL.UPDATE_ORDER, requestBody);
};

const cancelOrder = (loginID, token, invoiceId) => {
    const requestBody = { login_id: loginID, token, invoice_value: invoiceId };
    return AxiosInstance.post(URL.CANCEL_ORDER, requestBody);
};

const uploadFile = (loginID, token, fileName, file) => {
    const formData = new FormData();
    formData.append('login_id', loginID);
    formData.append('token', token);
    formData.append('description', fileName);
    formData.append('doc', file);

    return AxiosInstance.post(URL.SAVE_DOCUMENT, formData, {
        'content-type': 'multipart/form-data',
      });
};

export default { getAllOrder, getOrder, addOrder, updateOrder, cancelOrder, uploadFile}; 